.list {
  min-height: 200px; }
  .list .item {
    border: 2px solid #f7f7f7;
    background: #f7f7f7;
    line-height: 24px;
    padding: 8px 40px 8px 16px;
    margin: 8px 0;
    border-radius: 4px;
    position: relative;
    cursor: pointer;
    /*
    }*/ }
    .list .item .title {
      font-weight: bold; }
      .list .item .title .cat {
        color: #6db33f;
        font-weight: normal; }
    .list .item .desc {
      color: #706c69;
      font-size: 0.9rem; }
    .list .item .incompatible {
      display: none;
      color: #f30808;
      font-weight: bold; }
    .list .item a.btn-ico {
      position: absolute;
      right: 2px;
      top: 50%;
      margin-top: -16px;
      display: none; }
      .list .item a.btn-ico:hover {
        color: #000; }
    .list .item.active {
