      border-color: #ffe2e2; }
  .list.light .item {
    background: #fff; }
    .list.light .item.active {
      background-color: #e7f1f4;
      border-color: #e7f1f4; }
    .list.light .item.invalid a.btn-ico {
      display: block; }
    .list.light .item:hover a.btn-ico {
